.notifications {
  bottom: 1%;
  left: calc(50% - 200px);
  position: absolute;
  z-index: 10000000;
}

.notifications div {
  box-shadow: 0 12px 15px 0 rgba(0, 0, 0, 0.22), 0 17px 20px 0 rgba(0, 0, 0, 0.12);
  border: 1px solid;
  border-radius: 2px;
  cursor: pointer;
  margin-bottom: .5em;
  min-height: 3em;
  opacity: 1;
  padding: 1em;
  text-align: center;
  transition: opacity .5s linear;
  width: 400px;
}

.notifications div.clicked {
  opacity: 0;
}

.notifications div.success {
  background-color: #dff2bf;
  border-color: #9fd840;
  color: #4a6a15;
}

.notifications div.error {
  background-color: #ffbaba;
  border-color: #ff8787;
  color: #870000;
}

.notifications div.warn {
  background-color: #feefb3;
  border-color: #fccf1c;
  color: #8c7102;
}

.notifications div.info {
  background-color: #bde5f8;
  border-color: #33afe9;
  color: #1175a5;
}

.no-scroll {
  overflow: hidden;
}

.modal-container {
  background-color: rgba(0, 0, 0, 0.4);
  bottom: 0;
  display: flex;
  left: 0;
  overflow-y: auto;
  padding: 10vh 0;
  position: fixed;
  right: 0;
  top: 0;
  visibility: hidden;
  z-index: 1000;
}

.modal-container.animated {
  transition: all .3s;
}

.modal-container.modal-closed {
  opacity: 0;
}

.modal-container.modal-open {
  opacity: 1;
  visibility: visible;
}

.modal {
  box-shadow: 0 12px 15px 0 rgba(0, 0, 0, 0.22), 0 17px 20px 0 rgba(0, 0, 0, 0.12);
  background-color: #fff;
  margin: auto;
  max-width: 100%;
  width: 450px;
}

.modal.animated {
  transition: all .3s;
}

.modal.wide {
  width: 650px;
}

.modal.modal-closed {
  opacity: 0;
  transform: translateY(-100%);
}

.modal .title {
  background-color: #dbe9ee;
}

.modal .title .right {
  color: #24343a;
  cursor: pointer;
}

.modal .title .right:hover {
  color: #385059;
}

.modal .title,
.modal .body {
  padding: .75em;
}

.modal input,
.modal select {
  margin-bottom: 7px;
}

.modal [multiple] {
  height: 4em;
}

.modal .half {
  display: flex;
  justify-content: space-between;
}

.modal .half label {
  display: block;
  width: 49%;
}

.modal .buttons {
  float: right;
  margin: 1em;
  margin-right: 0;
}

.modal .buttons .flat {
  background-color: #fff;
  color: #24343a;
  line-height: 1.1em;
}

.collapsed .context-menu-container {
  display: none;
}

.context-menu-container {
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.15), 0 4px 14px 0 rgba(0, 0, 0, 0.12);
  background-color: #fff;
  border-radius: 3px;
  color: #24343a;
  padding: 5px 0;
  position: fixed;
  width: 200px;
  z-index: 100;
}

tb-context-menu-item {
  cursor: pointer;
}

tb-context-menu-item:hover {
  background-color: #f2f2f2;
}

tb-context-menu-item[isseparator] {
  cursor: default;
  height: 1em;
}

tb-context-menu-item[isseparator] {
  background-color: #fff;
}

.file-viewer {
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12), 0 3px 1px -2px rgba(0, 0, 0, 0.2);
  background-color: #fff;
  bottom: 2rem;
  left: 2rem;
  position: absolute;
  right: 2rem;
  top: 5rem;
}

.file-viewer .header {
  background-color: #dbe9ee;
  border-bottom: 1px solid #d8d8d8;
  font-family: Raleway;
  font-size: 24px;
  font-weight: 500;
  padding: 7px;
}

.file-viewer .header .right {
  font-size: 1rem;
}

.file-viewer .header a {
  background: none;
  color: #2d8097;
  text-shadow: none;
}

.file-viewer .header a:hover {
  text-decoration: underline;
}

.file-viewer .content {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 2.45rem;
}

.file-viewer .content iframe {
  border: none;
  border-radius: 0;
  height: 100%;
}
